import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { AboutModel } from 'src/app/models/about.model';
import { Observable } from 'rxjs';
import { HeaderModel } from '../models/header.model';
import { TimelineItemModel } from '../models/timeline.models';

const url = {
  header: environment.production ? '' : 'assets/data/header.config.json',
  about: environment.production ? '' : 'assets/data/about.config.json',
  experiences: environment.production ? '' : 'assets/data/experiencia.config.json',
  educations: environment.production ? '' : 'assets/data/educacion.config.json',
};

@Injectable({
  providedIn: 'root'
})
export class AppService {

  constructor(private http: HttpClient) { }

  getHeaderData(): Observable<HeaderModel> {
    return this.http.get<HeaderModel>(url.header);
  }

  getAboutData(): Observable<AboutModel> {
    return this.http.get<AboutModel>(url.about);
  }

  getEducationsData(): Observable<TimelineItemModel[]> {
    return this.http.get<TimelineItemModel[]>(url.educations);
  }

  getExperiencesData(): Observable<TimelineItemModel[]> {
    return this.http.get<TimelineItemModel[]>(url.experiences);
  }
}
